import { Component, h, Prop } from '@stencil/core';
import { isExternalUrl } from '../../utils';
import { ALERT_BUTTON_ALERT_TYPE, ALERT_BUTTON_SIZES, ALERT_BUTTON_TYPE, ALERT_BUTTON_VARIANTS } from './exports';

/**
 * @slot - For Label and Icon
 */
@Component({
  tag: 'mdx-alert-button',
  styleUrl: 'alert-button.scss',
  shadow: true,
})
export class AlertButton {
  @Prop()
  disabled = false;

  @Prop({ reflect: true })
  variant: ALERT_BUTTON_VARIANTS = ALERT_BUTTON_VARIANTS.PRIMARY;

  @Prop({ reflect: true })
  alertType: ALERT_BUTTON_ALERT_TYPE = ALERT_BUTTON_ALERT_TYPE.SUCCESS;

  @Prop({ reflect: true })
  size: ALERT_BUTTON_SIZES = ALERT_BUTTON_SIZES.MD;

  @Prop()
  type: 'button' | 'submit' | 'reset' = 'submit';

  @Prop({ reflect: true })
  as: ALERT_BUTTON_TYPE = ALERT_BUTTON_TYPE.BUTTON;

  @Prop()
  href: string;

  @Prop()
  ariaLabel: string;

  @Prop()
  target: '_blank' | '_self' | '_parent' | '_top' = '_self';

  private isExternalURL() {
    return isExternalUrl(this.href);
  }

  render() {
    if (this.as === ALERT_BUTTON_TYPE.BUTTON) {
      return (
        <button part="alert-button" class="alert-button" type={this.type} disabled={this.disabled}>
          <slot></slot>
        </button>
      );
    }
    return (
      <a
        part="alert-button"
        class="alert-button"
        href={this.href}
        aria-label={this.ariaLabel}
        target={this.target}
        rel={this.isExternalURL() ? 'noreferrer' : undefined}
      >
        <slot></slot>
      </a>
    );
  }
}
